import addressItem from '../../../components/address.vue'
import tips from '../../../components/tips.vue'

let $self
export default {
	components: {
		'address-item': addressItem,
		'tips': tips
	},
	data() {
		return {
			uuid: '',
			tips: '没找到地址，请新增地址!',
			addressList: [], // 返回的地址列表
			fromOrder: ''
		};
	},
	computed: {
		defaultAddress() {
			return this.addressList.find(item => item.defaultAddress)
		},
		otherAddressList() {
			return this.addressList.filter(item => !item.defaultAddress)
		}
	},
	onLoad(options) {
		if (JSON.stringify(options) !== '{}') {
			this.fromOrder = options.fromOrder
		}
	},
	onShow() {
		$self = this
		this.uuid = uni.getStorageSync('uuid')
		this.queryAddList()
	},
	methods: {
		// 请求地址列表
		queryAddList() {
			uni.request({
				url: 'https://messagecome.com/user/address/list',
				data: {
					uuid: $self.uuid
				}
			}).then(infoRes => {
				let [err, res] = infoRes
				if (res.data && res.data.status === 1) {
					$self.addressList = res.data.data
				}
			})
		},
		checkItem(address) {
			if (this.fromOrder === '0') {
				uni.setStorageSync('checkAddress', address)
				uni.navigateBack({})
			}
		},
		// 编辑地址
		goEdit(address) {
			let addStr = encodeURIComponent(JSON.stringify(address))
			uni.navigateTo({
				url: `./edit?address=${addStr}`
			})
		},
		// 新增地址
		goNew() {
			uni.navigateTo({
				url: './edit'
			})
		}
	}
}
